.TimeFeature{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50% , -50%);
  }
  
  .TimeFeature p{
    color: white;
    font-weight: 600;
    font-size: 48px;
  }
  
  .HeartRateFeature{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50% , -50%);
    color: white;
    font-weight: 600;
    font-size: 48px;
  }
  
  .HeartRateFeature p{
   margin-top: 16px;
  }
  
  .HeartRateFeature i {
    color: red;
    animation: beat 1s infinite;
  }
  
  @keyframes beat {
    0% {
      transform: scale(1,1);
    }
    50% {
      transform: scale(1.2 ,1.2)
    }
    100% {
      transform: scale(1,1);
    }
  }